html {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
html, body, #root {
    height: 100%;
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #f7f7f7;
}

/* App Layout */
.app-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-bottom: 240px; /* Reserve space for fixed button bar */
    box-sizing: border-box;
}

.display-area {
    flex: 1;
    min-height: 0;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    box-sizing: border-box;
}

.display-text {
    font-size: 2rem;
    line-height: 1.2;
    word-break: break-word;
    padding: 0 1rem;
}

.position-image {
    max-width: 100%;
    max-height: 80vh;
    object-fit: contain;
    border-radius: 12px;
    box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.loading-spinner {
    position: absolute;
    top: 1rem;
    width: 100%;
    text-align: center;
    font-size: 1rem;
    color: #888;
    pointer-events: none;
}

/* Fixed Button Bar */
.button-bar-fixed {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    border-top: 1px solid #ccc;
    padding: 1rem;
    box-sizing: border-box;
    z-index: 10;
}

/* Main Buttons */
.main-buttons {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    margin-bottom: 0.75rem;
}

.pick-button {
    width: 100%;
    padding: 1.25rem;
    font-size: 1.5rem;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: background-color 0.2s ease, transform 0.05s ease;
    touch-action: manipulation;
}

.pick-button:hover {
    background-color: #45a049;
}

.pick-button:active {
    transform: scale(0.97);
}

.pick-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/* Undo/Redo Buttons */
.undo-redo-bar {
    display: flex;
    justify-content: space-between;
    gap: 0.75rem;
}

.small-button {
    flex: 1;
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background-color: #ccc;
    color: #222;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.2s ease;
    touch-action: manipulation;
}

.small-button:hover:not(:disabled) {
    background-color: #bbb;
}

.small-button:active:not(:disabled) {
    transform: scale(0.96);
}

.small-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.history-columns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    padding: 1rem;
}

.history-column {
    flex: 1;
    min-width: 300px;
}

.history-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.history-image-item,
.history-text-item {
    padding: 0.5rem 0;
    border-bottom: 1px solid #ddd;
}

.history-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    object-fit: contain;
}

/* Target only the image list for grid layout */
.history-column.images .history-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
    gap: 0.75rem;
}

/* Remove bottom border in grid cells */
.history-column.images .history-image-item {
    border-bottom: none;
    padding: 0;
}

.history-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 0.5rem;
    padding: 1rem;
    box-sizing: border-box;
}
